<template>
  <div class="practice">
    <div style="margin: 10px 0;">
      <span class="demonstration">选择日期查看作业列表：</span>
    <el-date-picker
      @change="getTaskList"
      v-model="timeDate"
      type="date"
      placeholder="选择日期">
    </el-date-picker>
    </div>
    <el-checkbox :value="item.isComplete" @change="checkboxClick(item)" v-for="item in practiceList" :key="item.id" :label="item.workname"></el-checkbox>
  </div>
</template>

<script>
import { getTaskList, submitTask } from '../../../api/students.js'
export default {
  data() {
    return {
      checkList: [],
      practiceList: [
      ],
      isLoading: false,
      timeDate: new Date()
    }
  },

  created() {
    console.log(123)
    this.getTaskList()
  },

  methods: {
    // 获取练习列表
    async getTaskList() {
      const { data: res } = await getTaskList({
        time: this.timeDate,
        type: 2
      })
      console.log(res)
      if (res.data.length === 0) {
        this.practiceList = []
        return
      }
      this.practiceList = res.data[0].practiceList.map(item => {
        return {
          ...item,
          isComplete: item.isComplete === 2
        }
      })
    },
    // 提交作业
    async checkboxClick(item) {
      try {
        await this.$confirm('是否确认提交作业?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
      } catch (err) {
        return
      }
      this.isLoading = true
      const { data: res } = await submitTask(item.id, item.isComplete ? 1 : 2)
      if (res.code !== 0) return this.$message('提交失败，稍后重试')
      this.isLoading = false
      this.$message.success('提交成功')
      item.isComplete = !item.isComplete
    }
  }
}
</script>

<style scoped lang='less'>
.practice {
  padding: 0 15px;
}
.el-checkbox {
  display: block;
  margin: 15px 0;
}
</style>
